*{
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html, 
body{
 color: white;
 background-color: #1b1b32;
 font-family: Lato, sans-serif;
 margin-top: 40px;
}

.App{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

h1{
	color: #1b1b32;
	font-size: 25px;
	font-family: Roboto, sans-serif;
	text-align: center;
	margin: 20px 0;
}


.main-task-container {
	width: 600px;
	min-height: 500px;
	background-color: #d0d0d5;
	padding: 25px;
	margin: 10px;
	justify-content: center;
	border-radius: 26px;
}